<template>
    <el-container v-loading="loading">
        <div class="rise x add">
            <div class="title">{{title}}</div>
            <div class="content">
                <div class="item" v-for="(item, index) in data" :key="index">
                    <span>{{item.name}}</span>
                    <div v-if="!item.type">
                        <el-input v-if="item.module === 'name'" v-model="name" :placeholder="item.placeholder"/>
                        <el-input v-if="item.module === 'contactPerson'" v-model="contactPerson" :placeholder="item.placeholder"/>
                        <el-input v-if="item.module === 'mobile'" v-model="mobile" :placeholder="item.placeholder"/>
                        <el-input v-if="item.module === 'address'" v-model="address" :placeholder="item.placeholder"/>
                        <el-input v-if="item.module === 'memo'" v-model="memo" :placeholder="item.placeholder"/>
                    </div>
                </div>
            </div>
            <div class="btn-list">
                <el-button @click="cancle()" class="btn">取消</el-button>
                <el-button @click="save()" class="btn">保存</el-button>
            </div>
        </div>
    </el-container>
</template>

<script>
import queryString from "querystring";
import { DateDay } from 'utils/formData';
const titles = ["新建", "编辑"];
const defaultV = [
    { name:'校区名称：', placeholder:'请输入校区名称', module:'name'},
    { name:'联系人姓名：', placeholder:'请输入联系人姓名', module:'contactPerson'},
    { name:'联系人手机：', placeholder:'请输入联系人手机', module:'mobile'},
    { name:'校区地址：', placeholder:'请输入校区地址', module:'address'},
    { name:'备注：', placeholder:'请输入备注', module:'memo'},
]

export default {
    name: "add",
    data() {
        return {
            title: "",
            id: "",
            type: "",
            data: defaultV,
            name:'',
            contactPerson:'',
            mobile:'',
            address:'',
            memo:'',
            loading: false
        };
    },
    mounted() {
        this.dataInit();
    },
    methods: {
        dataInit() {
            const { type, id } = this.$route.query;
            this.type = type;
            this.id = id;
            if (type !== "edit") return (this.title = titles[0]);
            this.title = titles[1];
            this.detailAjax();
        },
        editInit(res){
            const { name, address, mobile, contactPerson, memo } = res.data;
            this.name = name;
            this.address = address;
            this.mobile = mobile;
            this.contactPerson = contactPerson;
            this.memo = memo;
        },
        detailAjax() {
            //查看详情
            this.loading = true;
            this.api.get(`/school/${this.id}`).then((res)=>{
                this.loading = false;
                this.editInit(res);
            }).catch((err) => {
                console.log(err)                
                this.loading = false;
                this.$message(err.message)
            })
        },
        editAjax(){
            const params = {
                name: this.name,
                address: this.address,
                mobile: this.mobile,
                contactPerson: this.contactPerson,
                memo: this.memo,
            }
            const axiosType = ['post','put'];
            let type = axiosType[0];
            if (this.type === "edit"){
                params.id = this.id;
                type = axiosType[1]
            }
            this.loading = true;       
            this.api[type](`/school`, params, 'form').then((res)=>{
                this.loading = false;
                this.$router.go(-1);
            }).catch((err) => {
              console.log(err)
              this.$message(err.message)
            })
        },
        save() {
            this.editAjax();
        },
        cancle() {
            //取消
            this.$router.go(-1);
        }
    }
};
</script>
 
